.contain-form{
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: center;

}
.editContaint{
    display: flex;
    justify-content: center;
}
form{
    background-color:rgb(5, 5, 242, 0.5);
    color: white;
    width: 600px;
    border-radius: 10px;
    border: 4px solid white;

    margin: 10px;
    padding: 10px;
    display: flex;
    justify-content: center;
}
table{
    width: 598px;
    border: 4px solid white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;


}
tr{
    display: flex;
    justify-content: center;
}
label{
    width: 400px;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    font-size: 18px;
}
input{
    border-radius: 10px;
    padding: 10px;
    width: 250px;
    border: none;
}
input:hover{
    border: 2px solid white;
    background-color:#8282F8;

}
.submitBTN{
    width: 600px;
    height: 40px;
    color: #8282F8;
    background-color: blue;
    font-size: 20px;
    border-radius: 10px;
    border: none;
}
.btn{
    width: 100px;
    padding: 5px;
    border-radius: 10px;
    background-color: antiquewhite;
}
.btn:hover{
    
    background-color: white;
    color: #8282F8;
}
.link{
    color: white;
    text-decoration: none;
    font-size: 16px;
  }
